import React from 'react';
import ReactDOM from 'react-dom/client';
import {HashRouter as Router,Routes, Route, Link} from 'react-router-dom';
//import {BrowserRouter as Router,Routes, Route, Link} from 'react-router-dom';
import './index.css';
import App from './App';
import reportWebVitals from './reportWebVitals';

const root = ReactDOM.createRoot(document.getElementById('root'));
let defaultTopic = 'rcnn'
if (sessionStorage.getItem('topic')) {defaultTopic = sessionStorage.getItem('topic')}
root.render(
  <React.StrictMode> 
  <Router>
    <Routes>
    <Route path="/rcnn/" element={<App topic="rcnn"/>} />
    <Route path="/yolo/" element={<App topic="yolo"/>} />
    <Route path="/cnn/" element={<App topic="cnn"/>} />
    <Route path="/segm/" element={<App topic="segm"/>} />
    <Route path="/" element={<App topic={defaultTopic}/>} />
    </Routes>
    </Router>
  </React.StrictMode>
);


//<Router basename='ai-blog/'>
